<template>
    <!-- 团队排行 -->
    <div class="wrap-div" ref="wrapHeight">
        <div ref="bar_chart" class="bar-chart" :style = "{width:wrapWidth+'px',height:wrapHeight+'px'}"></div>
    </div>
</template>

<script>
    const echarts = require('echarts/lib/echarts');
    require('echarts/lib/chart/bar');
    require('echarts/lib/chart/line');
    require('echarts/lib/component/tooltip');
    require('echarts/lib/component/legend');
    export default {
        data() {
            return {
                empnameData:[],
                orderamountsData:[],
                sendamountsData:[],
                percentData:[],
                wrapHeight: 189,
                wrapWidth: 525,
            }
        },
        mounted() {
            this.init();
            this.getDivHeight();
        },

        watch:{
            '$store.state.weekDataI':'getlist'
        },

        methods: {
            init() {
               this.getlist();
            },
            getDivHeight(){
                this.wrapHeight = this.$refs.wrapHeight.offsetHeight+40;
                this.wrapWidth = this.$refs.wrapHeight.offsetWidth;
            },
            
            //获取数据
            getlist(){
                this.fetch('/mktmfc/forms/getteamranking?datetype='+this.$store.state.weekTextI, {})
                .then((result)=>{
                    if(result.status === 200){
                        this.empnameData = result.data.empnamelist
                        this.orderamountsData = result.data.orderamountslist.map(item => {
                                            return (item/10000).toFixed(2)
                                        });
                        this.sendamountsData = result.data.sendamountslist.map(item => {
                                            return (item/10000).toFixed(2)
                                        });;
                        this.percentData = result.data.percentlist
                    }
                })
                .then(()=>{
                    this.initEchart();
                    this.setEchart(this.empnameData,this.orderamountsData,this.sendamountsData,this.percentData);
                })
            },

            //echart
            initEchart(){
                this.bar_chart = echarts.init(this.$refs.bar_chart);
            },

            //传参
            setEchart(empnameData,orderamountsData,sendamountsData,percentData) {
                //bar
                this.bar_chart.setOption({
                    tooltip : {
                        trigger: 'axis',
                        axisPointer : {
                            type : 'shadow'
                        }
                    },
                    legend: {
                        data:['计划额','销售额','完成率']
                    },
                    grid: {
                        left: '10%',
                        bottom: '25%',
                        containLabel: true
                    },
                    xAxis : [
                        {
                            type : 'category',
                            data : empnameData,
                           axisTick: {
                                alignWithLabel: true
                            }
                        }
                    ],
                    yAxis : [
                        {
                            type: 'value',
                            name: '万(元)',
                        },
                        {
                            type: 'value',
                            name: '',
                            max: 200,
                            min: 0,
                            interval: 40,
                            axisLabel: {
                                formatter: '{value} %'
                            }
                        },
                    ],
                    series : [
                        {
                            name:'计划额',
                            type:'bar',
                            itemStyle: {
                                normal: {
                                    color: '#cfe8dc',
                                    shadowColor: 'rgba(0, 0, 0, 0.5)',
                                    shadowBlur: 10
                                }
                            },
                            silent: true,
                            barWidth: '30%',
                            barGap: '-150%', // Make series be overlap
                            data:orderamountsData
                        },
                        {
                            name:'销售额',
                            type:'bar',
                            itemStyle: {
                                normal: {
                                    color: '#008ae0',
                                    shadowColor: 'rgba(0, 0, 0, 0.5)',
                                    shadowBlur: 10
                                }
                            },
                            barWidth: '30%',
                            data:sendamountsData
                        },
                        {
                            name:'完成率',
                            type:'line',
                            yAxisIndex: 1,
                            lineStyle: {
                                normal: {
                                    width: 3,
                                    shadowColor: 'rgba(0,0,0,0.4)',
                                    shadowBlur: 10,
                                    shadowOffsetY: 10
                                }
                            },
                            data:percentData
                        }
                    ]
                });
            }
        }
    }
</script>

<style scoped lang="less" type="text/less">
    .wrap-div {
        display: flex;
        height: 100%;
    }
    .bar-chart {
        /*width: 750px;*/
        /*height: 300px;*/
    }
</style>


